import AUTH from '@/config/web/auth'
import UTIL from '@/common/util'
import apiService from "@/config/web/apiService";
import {
    API_URL_BROADCAST_UPDATE_HOST_START_TIME,
    API_URL_BROADCAST_HOST_LIVE_INFO,
    API_URL_BROADCAST_LIVE_UPDATE,
    API_URL_BROADCAST_LIVE_END,
    API_URL_BROADCAST_LIVE_USER_OUT,
    API_URL_BROADCAST_LIVE_END_USER_INFO,
    API_URL_BROADCAST_LIVE_UPDATE_CHAT
} from "@/config/web/urls";
import jsonRpcService from "@/common/liveStreamJsonRpcService";
import JQ from "jquery";


export default {
    DEBUG: false,
    id: '',
    uid: '',
    streamRoomId: '',
    liveIngTimeKey: '',
    liveIngTime: '',
    vue: null,
    chatProcess: false,
    init(params, vueObj, DEBUG) {
        this.DEBUG = DEBUG;
        this.id = params.id;
        this.uid = params.uid;
        this.streamRoomId = this.id + '_' + this.uid;
        this.liveIngTimeKey = this.streamRoomId + '_liveStramHostTime';
        this.vue = vueObj;

    },
    getLiveInfo(func) {

        const frm = {id: this.id}
        apiService.post(API_URL_BROADCAST_HOST_LIVE_INFO, frm, (ret) => {
            let nowDate = new Date();
            if (ret.info.start_time) {
                nowDate = new Date(ret.info.start_time);


            }
            this.liveIngTime = parseInt(nowDate.getTime());
            func(ret);

        })
    },
    getStartTime() {
        let ingTime = parseInt(this.liveIngTime);
        let nowDate = new Date();
        let timeCnt = (nowDate.getTime() - ingTime) / 1000;
        timeCnt = Math.floor(timeCnt);
        if (timeCnt < 0) timeCnt = 0;
        return timeCnt;
    },
    updateLiveTime() {

    },
    liveTimeRemove() {
    },
    joinSocketLiveStream() {
        let frm = {
            uid: this.uid,
            roomId: this.streamRoomId,
            roomHost: 'yes',
        }

        jsonRpcService.init(frm, this.vue, this.DEBUG);

        const params = {id: this.id.toString()}
        apiService.post(API_URL_BROADCAST_UPDATE_HOST_START_TIME, params, (ret) => {

        })

    },
    setTimeVal(sec) {
        sec = parseInt(sec);
        if (isNaN(sec)) return '00:00:00';
        if (sec < 0) return '00:00:00';
        let resultSec = sec % 60;
        resultSec = Math.floor(resultSec)
        let min = sec / 60;
        min = Math.floor(min);
        let resultMin;
        if (min >= 60) {
            resultMin = min % 60;
        } else {
            resultMin = min;
        }
        resultMin = Math.floor(resultMin);
        let hour = min / 60;
        hour = Math.floor(hour);
        let resultHour;
        let day = '';
        if (hour >= 24) {
            resultHour = hour % 24;
        } else {
            resultHour = hour;
        }
        resultHour = Math.floor(resultHour);
        return day + this.zf(resultHour) + ':' + this.zf(resultMin) + ':' + this.zf(resultSec);
    },
    zf(date) {
        date = parseInt(date);
        if (date < 10) {
            return "0" + date;
        } else return date;
    },
    doEndMeetingActive(mobile) {
        if(mobile) {
            this.mobileRecodeChatAct()
        } else {
            this.recodeChatAct()
        }
        jsonRpcService.close("host")
        const frm = {id: this.id.toString()}
        apiService.post(API_URL_BROADCAST_LIVE_END, frm, (ret) => {


        })

    },
    mobileRecodeChatAct() {
            const chatDialog = JQ('.zmwebsdk-makeStyles-chatCustomize-10');
            if(chatDialog.html()) {
                this.mobileSaveChatAct();
            } else {
                const morePannel = JQ('#menu-list-icon-more');
                if (morePannel.html()) {
                    JQ('#menu-list-icon-more li:nth-child(2)').click();
                    setTimeout(() => {
                       this.mobileSaveChatAct();
                    }, 500);
                } else {
                    const moreButton = JQ('#meetingSDKElement div.zmwebsdk-MuiPaper-root.zmwebsdk-makeStyles-root-58.zmwebsdk-MuiPaper-elevation1.zmwebsdk-MuiPaper-rounded > div:nth-child(5) > button');
                    JQ(moreButton).click();
                    setTimeout(() => {
                        JQ('#menu-list-icon-more li:nth-child(2)').click();
                        setTimeout(() => {
                            this.mobileSaveChatAct();
                        }, 500);
                    }, 500);
                }
            }


    },
    mobileSaveChatAct() {
        if (this.chatProcess) return;
        else this.chatProcess = true;

        let chatContainer = JQ('.ReactVirtualized__Grid__innerScrollContainer > li');
        let from;
        let to;
        let msg;
        let chatDatList = [];
        chatContainer.each(function () {
            from = JQ(this).find('button:eq(0)').text();
            to = JQ(this).find('button:eq(1)').text();
            msg = JQ(this).find('.zmwebsdk-MuiListItemText-root span').text();


            if (from && to) {
                chatDatList.push({to: to, from: from, time:'', msg: msg});
            } else {
                chatDatList.push({to: '', from: '', time: '', msg: msg});
            }

        });
        let params = {
            message: JSON.stringify(chatDatList),
            parentId: this.id.toString(),
        }
        this.updateChat(params);

    },
    recodeChatAct() {

        let chatButton = JQ('#wc-content #wc-footer .footer-chat-button button');
        if (JQ('#wc-container-right')) {
            if (JQ('#wc-container-right .chat-container').html()) {
                this.saveChatAct();
            } else {
                if (chatButton) {
                    chatButton.click();
                    let obj = this;
                    setTimeout(function () {
                        obj.saveChatAct();
                    }, 500);
                }
            }
        } else if (chatButton) {
            chatButton.click();
            let obj = this;
            setTimeout(function () {
                obj.saveChatAct();
            }, 500);
        }
    },
    saveChatAct() {
        if (this.chatProcess) return;
        else this.chatProcess = true;

        let chatContainer = JQ('#wc-container-right .chat-container .ReactVirtualized__Grid__innerScrollContainer > div');
        let from;
        let to;
        let time;
        let msg;
        let chatDatList = [];
        chatContainer.each(function () {
            from = JQ(this).find('.chat-item__chat-info-header').find('span:eq(0)').text();
            to = JQ(this).find('.chat-item__chat-info-header').find('span:eq(2)').text();

            time = JQ(this).find('.chat-item__chat-info-header').find('span:eq(4)').text();
            msg = JQ(this).find('.chat-message__text-content').text();
            if (from && to && time) {
                chatDatList.push({to: to, from: from, time: time, msg: msg});
            } else {
                chatDatList.push({to: '', from: '', time: '', msg: msg});
            }

        });
        let params = {
            message: JSON.stringify(chatDatList),
            parentId: this.id.toString(),
        }
        this.updateChat(params);

    },
    updateChat(frm) {
        apiService.post(API_URL_BROADCAST_LIVE_UPDATE_CHAT, frm, (ret) => {
            this.chatProcess = false;

        })
    }
}
